const token = uni.getStorageSync('token');
export default {
	data() {
		return {
			user: 'admin@gmail.com',
			psw: '123456',
		}
	},
	onLoad() {
	// console.log(token);
	},
	methods: {
		login() {
			if (this.user.length <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入用户名'
				})
				return
			}
			if (this.psw.length <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入密码'
				})
				return
			}
					var login_status = 'ok'
			uni.request({
			
				url: 'http://192.168.31.9/api/user/login',
				data: {
					username: this.user,
					pwd: this.psw
				},
				header:{
						
						// "Cookie":"user_id=token.user_id; login_status=isok; login_key=token.login_key",
						// "Cookie":"user_id=1; login_status=ok; login_key=6755d4bc360597c18784e950350b0c13"
						"token":uni.getStorageSync('token')
					},
				method:'POST',
				success: (res) => {
				
					// console.log(res);
					if (res.data.ret == 200) {

						uni.reLaunch({
							url: '/pages/index/index'
						})
					} else {
						uni.showToast({
							icon: 'none',
							title: '账号或密码错误'
						})
					}
					uni.setStorage({
						key: "token", //将用户信息储存到本地缓存中，重要的uuid及token
						data: res.data.data.token,
						success: function(res) {
							// console.log(res);
						}
					});
						

				},
			})
		}
	}
}
